.HotelsSection {
    position: fixed;
    top: 80px;
    left: 0;
    width: 100%;
    height: 100vh;
    background: white;
    z-index: 1;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;

}

.selectSorting{
    text-align: center;
    width: 120px;
    margin-left: 10px;
    border-radius: 10px;
    margin-right: 20px;
}

.labelCheckbox input{
    width: 25px;
    margin-right: 20px;
}

.buttonReset{
    width: 120px;
    height: 30px;
    border-color: #ff0000;
    color: #ff0000;
    border-radius: 10px;
    background-color: #fff;
}

.content {
    padding: 20px;
    color: rgb(0, 0, 0);
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.textH1 {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #262729;
    font-size: 45px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
    font-family: sans-serif;
}

.divButton {
    max-width: 1230px;
    width: 100%;
}

.buttonClose {
    width: 100px;
    background-color: white;
    border: 1px solid #ff0000;
    border-radius: 9px;
    color: red;

}

.hotel_list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 1280px;
    width: 100%;
    margin-bottom: 100px;
}

.hotel_list img {
    width: 100%;
}

.hotel_list div {
    padding: 17px;
    margin: 17px;
    width: 350px;
    height: 425px;
    border-radius: 15px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}


.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    transition: background-color 0.3s ease;
    padding: 30px 30px;
}

.modalContent {
    background-color: white;
    padding: 20px;
    margin: 20px;
    border-radius: 8px;
    max-width: 600px;
    width: 100%;
    max-height: 650px;
    height: 100%;
    z-index: 10;
    overflow: auto;
}

.sliderImage {
    width: 100%;
    height: auto;
    object-fit: cover;
    aspect-ratio: 16 / 9;
    border-radius: 12px;

}

.slick-arrow,
.slick-next {
    display: contents;
}

.divContainerFil_Sort {
    display: flex;
    max-width: 1230px;
    width: 100%;
    margin: 0 auto;
    justify-content: space-between;
    align-items: center;
}

.DivSortFilters{
    display: flex;
    align-items: center;
}

.divSortOrder{
    margin-right: 20px;
}

.divSortOrder select{
    border-radius: 10px;
    margin-left: 10px;
}


.noResults h2 {
    color: #ff0000;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media(max-width: 875px) {
    .hotel_list {
        justify-content: center;
    }

    .divContainerFil_Sort {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .DivSortFilters{
        flex-direction: column;
    }
    .selectSorting{
        margin: 20px 10px;
    }

    .divSortOrder{
        margin-bottom: 20px;
    }
    .labelCheckbox input{
        margin-bottom: 20px;
        margin-right: 0px;
    }
  
}


@media(max-width: 450px) {
    .hotel_list div {
        width: 300px;
        height: 415px;
    }
}

@media(max-width: 410px) {
    .hotel_list div {
        width: 260px;
        height: 354px;
    }
}

@media(max-width: 355px) {
    .hotel_list div {
        width: 220px;
        height: 325px;
    }
}